import * as React from 'react';
import { Link as GatsbyLink, navigate } from 'gatsby';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardMedia from '@material-ui/core/CardMedia';
import CardActions from '@material-ui/core/CardActions';
import Tooltip from '@material-ui/core/Tooltip';
import IconButton from '@material-ui/core/IconButton';
import { makeStyles, createStyles, Theme } from '@material-ui/core/styles';
import YoutubeIcon from '@material-ui/icons/YouTube';
import Skeleton from '@material-ui/lab/Skeleton';
import { ArtistIcon, SelectorIcon } from '../icons';
import NationAvatar from './NationAvatar';
import { ProgramPlaylist } from '../../graphql-types';

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      display: 'flex',
    },
    cardThumbnail: {
      flex: 'auto',
      maxWidth: '30%',
      minWidth: 100,
      backgroundColor:
        theme.palette.type === 'dark'
          ? theme.palette.grey[700]
          : theme.palette.grey[200],
      display: 'flex',
    },
    cardThumbnailLink: {
      flex: 1,
      display: 'flex',
      overflow: 'hidden',
    },
    cardThumbnailInner: {
      flex: 1,
      transition: theme.transitions.create('transform'),
      '&:hover': {
        transform: 'scale(1.2)',
      },
    },
  })
);

interface Props {
  tune: Pick<
    ProgramPlaylist,
    | 'artist'
    | 'youtube'
    | 'title'
    | 'selector'
    | 'indexInWeek'
    | 'corner'
    | 'nation'
    | 'year'
  >;
}

function TuneCard({ tune }: Props) {
  const classes = useStyles();
  return (
    <Box my={2}>
      <Card className={classes.root}>
        <Box className={classes.cardThumbnail}>
          {tune.youtube ? (
            <a
              className={classes.cardThumbnailLink}
              href={`https://youtu.be/${tune.youtube}`}
              target="_blank"
              rel="noopener noreferrer"
            >
              <CardMedia
                className={classes.cardThumbnailInner}
                image={`https://i.ytimg.com/vi/${tune.youtube}/0.jpg`}
                title={`${tune.artist}${tune.title}YouTube`}
              />
            </a>
          ) : null}
        </Box>
        <Box flex="1">
          <Box px={2} pt={1}>
            <Typography variant="body2">
              M{tune.indexInWeek}. {tune.corner}{' '}
              {tune.selector !== '' ? `${tune.selector}` : null}
            </Typography>
          </Box>
          <CardHeader
            avatar={<NationAvatar nation={tune.nation} />}
            title={tune.title}
            subheader={`${tune.artist} (${tune.year})`}
          />
          <CardActions>
            <Tooltip title={`${tune.artist}`}>
              <IconButton component={GatsbyLink} to={`/artist/${tune.artist}`}>
                <ArtistIcon />
              </IconButton>
            </Tooltip>
            {tune.selector !== '' ? (
              <Tooltip title={`${tune.selector}`}>
                <IconButton
                  component={GatsbyLink}
                  to="/selectors/"
                  state={{ selector: tune.selector }}
                >
                  <SelectorIcon />
                </IconButton>
              </Tooltip>
            ) : null}
            {tune.youtube ? (
              <Tooltip title="YouTube ">
                <IconButton
                  href={`https://youtu.be/${tune.youtube}`}
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  <YoutubeIcon />
                </IconButton>
              </Tooltip>
            ) : null}
          </CardActions>
        </Box>
      </Card>
    </Box>
  );
}

export default TuneCard;

export function TuneCardSkeleton() {
  const classes = useStyles();
  return (
    <Box my={2}>
      <Card className={classes.root}>
        <div className={classes.cardThumbnail}>
          <Skeleton variant="rect" />
        </div>
        <Box flex="1">
          <Box px={2} pt={1}>
            <Typography variant="body2">
              <Skeleton variant="text" />
            </Typography>
          </Box>
          <CardHeader
            avatar={<Skeleton variant="circle" width={40} height={40} />}
            title={<Skeleton variant="text" />}
            subheader={<Skeleton variant="text" />}
          />
          <CardActions>
            <Skeleton variant="circle" width={40} height={40} />
            <Skeleton variant="circle" width={40} height={40} />
            <Skeleton variant="circle" width={40} height={40} />
          </CardActions>
        </Box>
      </Card>
    </Box>
  );
}
